* {margin: 0; padding: 0;
  box-sizing: border-box;}
header {
  background-color: aliceblue;
  padding: 2rem;
  font-weight: bold;
}
.list {
  padding: 20px 10px;
  border-bottom: 1px solid lightgray;
  display: flex;
  flex-wrap: wrap;
  position: relative;
  background-color: blanchedalmond;
}
.listname {
  width: calc(100% - 100px);
  color: #E95A5A;
  font-size: 1.5rem;
}
.good {  width: 100px;}
.good>span {cursor: pointer;}
.date {
  width: 100%;
  font-size: 0.75rem;
  padding-top: 10px;
}
.modal {
  background-color: #eee;
  padding: 20px;
  position: absolute;
  top:100px;
  right: 0;
  width: 50%;
  z-index: 10;
}
.del {
  margin-top: 10px;
}
.input {
  padding: 1rem;
  background-color:azure;
}
.input button {
  margin-left: 1rem;
  background-color: #CDD2D0;
  border: 1px solid rgba(0,0,0,0.1);
  color: white;
  padding: 5px 1.5rem;
}
.good {
  position: absolute;
  top: 50%;
  right: 10px;
  transform: translateY(-50%);
}
.del {
  background-color: #CDD2D0;
  border: 1px solid rgba(0,0,0,0.1);
  color: white;
  padding: 5px 1.5rem;
}